\n                applicationID: props.parentId,\n                name: name.value,\n                description: description.value,\n            });\n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n\nfunction setDescValue(desc: string) {\n    description.value = desc;\n}\n\nfunction setDescValidationStatus(isValid: boolean) {\n    isDescValid.value = isValid;\n}\n\nfunction setNameValue(value: string) {\n    name.value = value;\n}\n\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\n</script>\n\n<template>\n    <div class=\"nameInputDiv\" style=\"padding-top: 32px;\">\n        <div class=\"input-label\">Module Name</div>\n        <IDEInput :isRequired=\"true\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" :placeholder=\"'Name'\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n        <div style=\"margin-top: 24px\">\n            <div class=\"input-label\">Description</div>\n            <IDEInput :isRequired=\"false\" :disable-required-icon=\"true\" @getValue=\"setDescValue\"\n                @validateStatus=\"setDescValidationStatus\" :placeholder=\"'Description'\" :type=\"'textarea'\"\n                :text-area-height=\"'166px'\" info-text=\"\" :validations=\"validations.description\" />\n        </div>\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emit('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid && isDescValid\" />\n</template>\n\n<style scoped>\n.input-label {\n    color: #000000;\n    font-weight: 500;\n    font-family: 'Roboto';\n    font-size: 14px;\n    margin-left: 6px;\n}\n\n.nameInputDiv {\n    padding-left: 20px;\n    padding-right: 20px;\n    padding-top: 18px;\n}\n</style>\n"],"sourceRoot":""}]);
